<template>
<div class="lunata-table">
    <div v-if="hasItems">
        <slot name="table">
            <table>
                <slot name="head">
                    <thead>
                        <th v-for="column in visibleColumns" :key="column.name">{{ column.name }}</th>
                        <th v-if="crudEnabled" />
                    </thead>
                </slot>
                <slot name="body">
                    <tbody>
                        <tr v-for="item in items" :key="item[pk]">
                            <template v-for="column in visibleColumns">
                                <lunata-cell :key="column.name" :item="item" :column="column" :items="items"></lunata-cell>
                            </template>
                            <td v-if="crudEnabled">
                                <a href v-for="(action, name) in rowActions(item)" :key="name" class="row-action-link" @click.prevent="action(item)">{{ name }}</a>
                            </td>
                        </tr>
                        <tr v-for="(method, name) in aggregates" :key="name">
                            <td :colspan="columns.length" class="aggregate-row">{{ name }}: {{ method(items) }}</td>
                        </tr>
                    </tbody>
                </slot>
            </table>
        </slot>
    </div>
    <div v-else>
        <p class="no-items-message">No {{ labels.plural }} were found.</p>
    </div>
    <template v-if="crudEnabled">
        <lunata-bootstrap-modal ref="createModal" :columns="columns" :labels="labels" type="create" @create="emitCreate" />
        <lunata-bootstrap-modal ref="editModal" :columns="columns" :labels="labels" type="edit" @update="emitUpdate" />
    </template>
</div>
</template>

<script>
import LunataBootstrapModal from './LunataBootstrapModal.vue'
import LunataCell from './LunataCell.vue'

export default {
    name: 'lunata-table',
    props: {
        'items': {
            type: Array, 
            required: true
        },
        'columns': {
            type: Array,
            required: true
        },
        'actions': {
            type: Object,
            default() {
                return null
            }
        },
        'appendActions': {
            default() {
                return {}
            }
        },
        'pk': {
            type: String,
            required: false,
            default: 'id'
        },
        'labels': {
            type: Object,
            required: false,
            default() {
                return {
                    singular: "item",
                    plural: "items"
                }
            }
        },
        'aggregates': {
            type: Object,
            required: false,
            default() {
                return {}
            }
        },
        'crudEnabled': Boolean
    },
    components: {
        LunataCell,
        LunataBootstrapModal
    },
    data() {
        return {}
    },
    computed: {
        hasItems() {
            return this.items.length > 0
        },
        visibleColumns() {
            return this.columns.filter(column => {
                return !column.hidden
            })
        },
    },
    mounted() {},
    methods: {
        rowActions(item) {
            let vm = this
            if (this.actions != null) {
                if (typeof this.actions == 'function') {
                    return this.actions(item)
                } else {
                    return this.actions
                }
            } else {
                if (typeof this.appendActions == 'function') {
                    return {
                        Edit(item) {
                            vm.edit(item)
                            vm.$emit('context-edit', item)
                        },
                        Delete(item) {
                            vm.$emit('context-delete', item)
                        },
                        ...vm.appendActions(item)
                    }
                } else {
                    return {
                        Edit(item) {
                            vm.edit(item)
                            vm.$emit('context-edit', item)
                        },
                        Delete(item) {
                            vm.$emit('context-delete', item)
                        },
                        ...vm.appendActions
                    }
                }
            }
        },
        create() {
            this.$refs.createModal.open()
        },
        edit(item) {
            this.$refs.editModal.open(item)
        },
        emitCreate(item) {
            this.$emit('create', item)
        },
        emitUpdate(item) {
            this.$emit('update', item)
        }
    },
}
</script>
<style lang="scss" scoped>
table {
    width: 100%;
}
thead {
    background: #F5F6FA;
    height: 45px;
}
th {
    font-size: 1rem;
    color: #A3A6B4;
    font-weight: bold;
    text-transform: uppercase;
}
th, td {
    padding: 10px 15px;
}
td {
    color: #4D4F5C;
    font-size: 1rem;
}
tr:hover {
    background: rgba(0,0,0,0.05);
}
tr + tr {
    border-top: 1px solid #F1F1F3;
}
.no-items-message {
    font-size: 1rem;
}
</style>
