<template>
    <div class="modal fade" ref="modal">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="modal-title">{{ title }}</h2>
                </div>
                <div class="modal-body">
                    <lunata-bootstrap-modal-field v-for="column in visibleColumns" ref="field" :key="column.name" :item="editingItem" :column="column" @update="update($event, column)" />
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                    <button type="button" class="btn" :class="buttonClass" @click="confirm">{{ button_text }}</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import $ from 'jquery'
import 'bootstrap'

import LunataBootstrapModalField from './LunataBootstrapModalField.vue'
export default {
    props: {
        type: {
            type: String,
            required: true,
            validator(val) {
                return ['create', 'edit', 'delete'].indexOf(val) !== -1
            }
        },
        columns: {
            type: Array,
            required: true
        },
        labels: Object,
        item: {
            type: Object,
            required: false
        }
    },
    components: {
        LunataBootstrapModalField
    },
    data() {
        return {
            editingItem: {}
        }
    },
    computed: {
        title() {
            switch (this.type) {
                case 'create': return 'Create ' + this.labels.singular; 
                case 'edit': return 'Edit ' + this.labels.singular; 
                case 'delete': return 'Delete ' + this.labels.singular; 
                default: return ''; 
            }
        },
        button_text() {
            switch (this.type) {
                case 'create': return 'Create'; 
                case 'edit': return 'Save'; 
                case 'delete': return 'Delete'; 
                default: return ''; 
            }
        },
        visibleColumns() {
            return this.columns.filter(column => {
                return !column.computed
            })
        },
        buttonClass() {
            return {
                'btn-success': this.type == 'create',
                'btn-primary': this.type == 'edit',
                'btn-danger': this.type == 'delete',
            }
        }
    },
    methods: {
        resetEditingItem() {
            this.editingItem = this.item ? Object.assign({}, this.item) : {}
        },
        resetInputs() {
            this.$refs.field.forEach(field => {
                field.clear()
            })
        },
        open(item) {
            $(this.$refs.modal).modal({
                backdrop: false
            })
            this.editingItem = Object.assign({}, item)
        },
        close() {
            $(this.$refs.modal).modal('hide')
        },
        confirm() {
            switch (this.type) {
                case 'create': this.$emit('create', this.editingItem); break;
                case 'edit': this.$emit('update', this.editingItem); break;
                case 'delete': this.$emit('delete', this.item); break;
            }
            this.resetEditingItem()
            this.resetInputs()
            this.close()
        },
        update($event, column) {

            var val

            if (column.type == 'checkbox') {
                val = $event.target.checked;
            } else if (column.type == 'number' || column.type == 'float') {
                val = parseFloat($event.target.value)
            } else if (column.type == 'int') {
                val = parseInt($event.target.value)
            } else {
                val = $event.target.value
            }
            
            let obj = {...this.editingItem}
            
            if (column.hasOwnProperty('set')) {
                column.set(obj, val)
            } else {
                if (column.hasOwnProperty('prop')) {
                    this.$set(obj, column.prop, val)
                }
            }

            this.editingItem = Object.assign({}, this.editingItem, obj)
        }
    }
}
</script>
