<script>
export default {
    props: ['column', 'item'],

    methods: {
        clear() {
            if ('input' in this.$refs) {
                this.$refs.input.value = ''
            }
        }
    },

    computed: {
        value() {
            // If this column specifies a 'get' method, try to use it to return the value
            if (this.column.hasOwnProperty('get') && typeof this.column.get == 'function') {
                try {
                    return this.column.get(this.item)
                } catch (e) {
                    // Failed to return a value using the 'get' method, so try to return a value using the 'prop'
                    if (this.column.hasOwnProperty('prop')) {
                        return this.item[this.column.prop]
                    } else {
                        // A prop wasn't provided, so there is no value to return
                        return null
                    }
                }
            } else {
                // The column did not specify a 'get' method, so try to return the value using 'prop'
                if (this.column.hasOwnProperty('prop')) {
                    return this.item[this.column.prop]
                } else {
                    // There is no 'prop', so there is no value to return
                    return null
                }
            }
        },
        display() {
            // If the column has a 'display' method, use it to transform the value before displaying
            if (this.column.hasOwnProperty('display') && typeof this.column.display == 'function') {
                return this.column.display(this.value)
            } else {
                return this.value
            }
        }
    },

    /* eslint-disable no-unused-vars */
    render(h) {
        let tag = null
        let update = e => {
            this.$emit('update', e)
        }

        var columnType = this.column.hasOwnProperty('type') ? this.column.type : 'text'

        var choices

        if (this.column.hasOwnProperty('choices')) {

            choices = this.column.choices

            if (Array.isArray(this.column.choices)) {
                choices = choices.map(choice => {
                    return {
                        key: choice,
                        value: choice
                    }
                })
            } else {
                choices = Object.keys(choices).map(key => {
                    return {
                        key: key,
                        value: choices[key]
                    }
                })
            }
        } else {
            choices = null
        }

        switch (columnType) {
            case 'select':
                tag = (
                    <select ref="input" class="form-control" value={this.value} onInput={update}>
                        {choices.map(choice => {
                            return (
                                <option key={choice.value} value={choice.value}>{ choice.key }</option>
                            )
                        })}
                    </select>
                )
            break;
            case 'textarea':
                tag = (
                    <textarea ref="input" class="form-control" value={this.value} onInput={update} />
                )
            break;
            case 'checkbox':
                tag = (
                    <input type="checkbox" ref="input" class="form-control" checked={this.value} onInput={update} />
                )
            break;
            case 'float':
            case 'int':
                columnType = 'number';
            /** eslint-disable no-fallthrough */
            default:
                tag = (
                    <input type={columnType} ref="input" class="form-control" value={this.value} onInput={update} />
                )
            break;
        }

        if (this.column.readonly) {
            tag = <div>{this.display}</div>
        }

        return (
            <div class="lunata-modal-field form-group">
                <label for={this.column.name}>{this.column.name}</label>
                {tag}
            </div>
        )
    }
}
</script>
